import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'

import Header from '../components/header'
import Footer from '../components/footer'
import './index.css'

const Layout = ({ children, data }) => (
  <div>
    <Helmet
      title={data.site.siteMetadata.title}
      meta={[
        { name: 'description', content: 'Fitato is Your One Pass to 250+ Best Gyms & Fitness Studios in Pune. Access All Gyms, Dance, Yoga, Crossfit and Swimming Near You.' },
      ]}
    >
      <script async src="https://www.googletagmanager.com/gtag/js?id=UA-71499025-1"></script>
      <script type="text/javascript">{`
        window.dataLayer = window.dataLayer || [];
        function gtag(){dataLayer.push(arguments);}
        gtag('js', new Date());

        gtag('config', 'UA-71499025-1');
      `}</script>
      <script type="text/javascript">{`
        !function(f,b,e,v,n,t,s)
        {if(f.fbq)return;n=f.fbq=function(){n.callMethod?
        n.callMethod.apply(n,arguments):n.queue.push(arguments)};
        if(!f._fbq)f._fbq=n;n.push=n;n.loaded=!0;n.version='2.0';
        n.queue=[];t=b.createElement(e);t.async=!0;
        t.src=v;s=b.getElementsByTagName(e)[0];
        s.parentNode.insertBefore(t,s)}(window, document,'script',
        'https://connect.facebook.net/en_US/fbevents.js');
        fbq('init', '1300826059946037');
        fbq('track', 'PageView');
      `}</script>
      <noscript>{`
        <img height="1" width="1" style="display:none"
        src="https://www.facebook.com/tr?id=1300826059946037&ev=PageView&noscript=1"
        />
      `}</noscript>
      <meta name="google-site-verification" content="i4HIHVSxdf3ZQQ0FwU5WIjVvuz4k5tY6te2tw_sVihA"/>
      <script type="text/javascript" src="https://www.usefomo.com/api/v1/H12aVavd2-9fGS3X1xbcZw/load.js" async></script>
      <link rel="icon" type="image/png" href='static/fitato-favicon.png' />
      <link href="https://fonts.googleapis.com/css?family=Roboto:400,400i,600,600i" rel="stylesheet" />
      <link rel="stylesheet" type="text/css" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css" />
    </Helmet>
    <Header siteTitle={data.site.siteMetadata.title} />
    <div className="main-header-placeholder" />
    <div>
      {children()}
    </div>
    <Footer />
    <script
      dangerouslySetInnerHTML={{
        __html: `
        adroll_adv_id = "TUU6453P5ZFKDGZPZWDNBT";
        adroll_pix_id = "4TDPYG7IRNDYTE72YXVTRL";
    
        (function () {
          var _onload = function(){
            if (document.readyState && !/loaded|complete/.test(document.readyState)){setTimeout(_onload, 10);return}
            if (!window.__adroll_loaded){__adroll_loaded=true;setTimeout(_onload, 50);return}
            var scr = document.createElement("script");
            var host = (("https:" == document.location.protocol) ? "https://s.adroll.com" : "http://a.adroll.com");
            scr.setAttribute('async', 'true');
            scr.type = "text/javascript";
            scr.src = host + "/j/roundtrip.js";
            ((document.getElementsByTagName('head') || [null])[0] ||
              document.getElementsByTagName('script')[0].parentNode).appendChild(scr);
            };
            if (window.addEventListener) {window.addEventListener('load', _onload, false);}
            else {window.attachEvent('onload', _onload)}
          }());
        `,
      }}
    />
  </div>
)

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`



